<template>
    <div >
        <ul class="lista list-group">
            <li class="bg-dark list-group-item text-light">Código      |       Nombre      |   Cantidad en stock   |       Precio</li>
            <li class="list-group-item text-light" :style="{background: juego.color}" v-for="(juego, index) in listaJuegos" :key="index">{{juego.codigo}}  |   {{juego.nombre}}    |   {{juego.stock}} |   ${{juego.precio}}</li>
        </ul>
    </div>
    
</template>

<script>
import {mapState} from 'vuex'

export default {
    computed:{
        ...mapState(['juegos']),
        listaJuegos(){
            return this.juegos
        }
    },
}
</script>

<style scoped>
.lista{
    max-width: 50%;
    list-style: none;
}

li{
    line-height: 3rem;
    color: white;
    text-shadow: black 1px 1px 2px;
    padding-left: 2rem;

}
</style>